import { isHsl } from './hsl';
import { normalizeRgb } from './rgb';
import hslToRgb from './transforms/hslToRgb';

function roundRgb(rgb) {
  return {
    r: Math.round(rgb.r),
    g: Math.round(rgb.g),
    b: Math.round(rgb.b),
    a: rgb.a
  };
}

function rgbToHexString(rgb) {
  var int = ((Math.round(rgb.r) & 0xff) << 16) + ((Math.round(rgb.g) & 0xff) << 8) + (Math.round(rgb.b) & 0xff);
  var str = int.toString(16).toUpperCase();
  var padLeft = '000000'.substring(str.length);
  return "#".concat(padLeft).concat(str);
}

function rgbToRgbaString(rgb) {
  rgb = roundRgb(rgb);
  return "rgba(".concat(rgb.r, ", ").concat(rgb.g, ", ").concat(rgb.b, ", ").concat(rgb.a, ")");
}

export default function colorString(color) {
  var rgbColor = isHsl(color) ? hslToRgb(color) : normalizeRgb(color);
  return rgbColor.a === 1 ? rgbToHexString(rgbColor) : rgbToRgbaString(rgbColor);
}